    Version: 1.0
-------------------------------------------------------------*/
/*--------------------------------
     =====Table Of Contents====

1.  General Styles 
2.  Mixins CSS
3.  Back To Top Button CSS
4.  Preloader CSS
5.  Button & Link CSS
6.  Section Title & Overlay & Shape CSS
7.  Social Link CSS
8.  Pagination CSS
9.  Sidebar CSS
     - Search Box Widget
     - Taglist Widget
     - Category Widget
     - Filter Search Widget
     - New Arrival Product Widget
     - Popular Post Widget
10. Sign In CSS
11. FAQ Terms & Conditions
----------------------------------*/
/*---------------------------------
            General Styles 
-----------------------------------*/
* {
  box-sizing: border-box;
}

.emergency-area {
  position: relative;
  background-image: url(https://seduloussoftech.com/PMCH/assets/img/emergency-bg.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
} 
.emergency-area::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right,#002F79,rgba(0,47,121,0.3294117647));
} 
.emergency-content {
  position: relative;
  z-index: 1;
} 
.emergency-content h2 {
  font-size: 35px;
  color: #fff;
  font-weight: 600;
  margin-bottom: 20px;
} 
.emergency-content h2 b {
  font-weight: 700;
  color: #1fa2ff;
}
.emergency-content p {
  color: #fff;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 500;
}
.emergency-content .emergency-icon-content {
  padding-left: 75px;
  position: relative;
}
.emergency-content .emergency-icon-content img {
  top:-5px;
  left: 0;
  position: absolute;
  font-size: 60px;
  color: #00e3e9;
  line-height: 1;
  height:60px;
}
.emergency-content .emergency-icon-content h3 a {
  color: #fff;
}
.emergency-shape {
  position: absolute;
  top: 0;
  left:126px;
  overflow: hidden;
}
/* .emergency-shape img {
  max-width: 884px;
} */

.h2 {
  font-weight: 700;
  line-height: 1.4;
  color: #000;
  font-family: "Inter", sans-serif;
}
html {
  scroll-behavior: smooth;
}

:focus,
button:focus,
.btn:focus {
  outline: 0;
  box-shadow: none;
}

.form-control:focus {
  box-shadow: none;
}

a,
button,
input[type="submit"] {
  cursor: pointer;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -webkit-appearance: textfield;
  appearance: textfield;
}

a {
  color: #000;
  text-transform: inherit;
  text-decoration: none;
  transition: all 0.3s ease;
}

a:hover,
a :focus {
  text-decoration: none;
  box-shadow: none;
  color: #000;
}

a:focus {
  outline: 0 solid;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  line-height: 1.4;
  color: #000;
  font-family: "Inter", sans-serif;
}

body {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  color: #666666;
}

p {
  font-size: 16px;
  line-height: 1.8;
  color: #666666;
