.btn {
  display: inline-block;
  /* line-height: 26px; */
  font-weight: 400;
  /* border-radius: 5px; */
  text-align: center;
  transition: all 0.7s ease-in-out;
  position: relative;
  z-index: 1;
  /* border: none; */
  /* padding: 14px 28px 14px; */
  /* border-radius: 0; */
  color: #fff;
  overflow: hidden;
  border: 1px solid transparent;
  white-space: nowrap;
}

.btn i {
  position: relative;
  top: 3px;
  font-size: 16px;
  margin-left: 7px;
  transition: all 0.4s ease-in-out;
}

.btn:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 50%;
  height: 0;
  visibility: hidden;
  opacity: 0;
  z-index: -1;
  border-radius: 0 50% 50% 0;
  transition: all 0.4s ease-in-out;
}

.btn:after {
  border-radius: 50% 0 0 50%;
  position: absolute;
  bottom: 0;
  right: 0;
  content: "";
  width: 50%;
  height: 0;
  visibility: hidden;
  opacity: 0;
  z-index: -1;
  transition: all 0.4s ease-in-out;
}

.btn:hover {
  color: #fff;
}

.btn:hover:after, .btn:hover:before {
  width: 100%;
  height: 100%;
  visibility: visible;
  opacity: 1;
}

