/* Navbar collapse */
.navbar-collapse {
  position: fixed;
  top: 0;
  right: -100%;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: #fff;
  padding: 20px;
  border-radius: 0 0 0 5px;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  transition: right 0.3s ease;
}

